import React, { useState } from "react";
import {
  Box,
  IconButton,
  CircularProgress,
  Menu,
  MenuItem,
  Dialog,
  DialogTitle,
  DialogContent,
  Typography,
  DialogActions,
  Button,
} from "@mui/material";

import Sort from "@mui/icons-material/Sort";
import Refresh from "@mui/icons-material/Refresh";
import Info from "@mui/icons-material/Info";

export default function UtilityIcons({
  trackClick,
  setLastUpdated,
  setSortType,
}) {
  const [isRefresh, setIsRefresh] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const handleRefresh = () => {
    trackClick("refresh");
    setTimeout(() => {
      setLastUpdated(Date.now());
      setIsRefresh(true);
    }, 100);
    setTimeout(
      () => setIsRefresh(false),
      (Math.floor(Math.random() * 3) + 1) * 1000
    );
  };
  const handleClose = (option) => {
    if (option) trackClick("sort-menu-option", { choice: option });
    setTimeout(() => {
      setAnchorEl(null);
      setSortType(option);
    }, 100);
  };
  const handleClickSort = (event) => {
    trackClick("sort-menu-open");
    const target = event.currentTarget;
    setTimeout(() => setAnchorEl(target), 100);
  };

  const [helpOpen, setHelpOpen] = useState(false);
  const handleInfoClick = () => {
    trackClick("help-button");
    setHelpOpen(true);
  };

  const handleInfoClose = () => {
    trackClick("help-button-close");
    setHelpOpen(false);
  };
  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "row",
        alignContent: "center",
        justifyContent: "center",
        height: "100%",
      }}
    >
      <Menu
        id="sort-menu"
        anchorEl={anchorEl}
        open={!!anchorEl}
        onClose={() => handleClose()}
        MenuListProps={{
          "aria-labelledby": "sort-button",
        }}
      >
        <MenuItem onClick={() => handleClose("name")}>Sort by name</MenuItem>
        <MenuItem onClick={() => handleClose("queue")}>Sort by queue</MenuItem>
        <MenuItem onClick={() => handleClose("canteen")}>
          Sort by canteen order
        </MenuItem>
      </Menu>
      <Dialog open={helpOpen} onClose={handleInfoClose}>
        <DialogTitle>Info</DialogTitle>
        <DialogContent dividers>
          <Typography gutterBottom>
            <b>Food Availability and Queue Status</b> (FAQS) is the
            quick-and-easy solution to check the availability of food in SUTD's
            canteen.
          </Typography>
          <Typography gutterBottom>
            To find out more about the offerings each stall has, click on their
            respective stall cards!
          </Typography>
          <Typography gutterBottom variant="body2">
            Developed for SUTD'23 60.005 Human Computer Interaction.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleInfoClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
      <IconButton
        size="large"
        aria-label="sort"
        onClick={handleClickSort}
        id="sort-button"
        sx={{
          height: "1dvh",
          width: "1dvw",
          ml: "2dvw",
          alignSelf: "center",
        }}
      >
        <Sort />
      </IconButton>
      <IconButton
        size="large"
        aria-label="refresh"
        onClick={handleRefresh}
        color="inherit"
        id="sort-button"
        sx={{ height: "1dvh", alignSelf: "center" }}
      >
        {isRefresh ? <CircularProgress size="2dvh" /> : <Refresh />}
      </IconButton>
      <IconButton
        aria-label="info"
        sx={{
          height: "1dvh",
          alignSelf: "center",
          mr: "-2dvw",
        }}
        onClick={handleInfoClick}
      >
        <Info />
      </IconButton>
    </Box>
  );
}
